export default {
	data() {
		return {
			login: false
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中'
		});
		this.goLogin();
	},
	methods: {
		goLogin() {
			uni.hideLoading();
			if (!this.login) {
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						console.log(loginRes);
						// 获取用户信息
						uni.getUserInfo({
							provider: 'weixin',
							success: function(infoRes) {
								console.log(infoRes);
								this.login = true;
							}
						});
					}
				});
			}
		},
		goToLikeTab() {
			uni.setStorageSync('likeTabsStatus', false);
			uni.reLaunch({
				url: '/pages/liketab/liketab'
			})
		}
	}
}
